.not-found-container {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
}

.not-found-header {
  text-align: center;
  grid-column: 3/span 8;
  grid-row: 1;
  margin-bottom: 0px;
  margin-top: 5px;
  font-size: 3em;
}

.not-found-paragraph {
  grid-column: 3/span 8;
  grid-row: 2;
  font-size: 1.5em;
  margin-top: 0px;
}

.not-found-paragraph a {
  color: #55c500;
}


.not-found-header,
.not-found-paragraph {
  color: #e3e3e3;
  background-color: #576f75;
}


