.wrapper {
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  margin-top: 5px;
  -ms-flex-pack: stretch;
      justify-content: stretch;
}

.grid-item {
  grid-column: 3 / span 8;
  background-color: #576f75;
  color: #e3e3e3;
  padding: 20px;
}

.resources-header {
  text-align: center;
  text-decoration: underline;
}

.text-center {
  text-align: center;
}

.resources-link {
  text-decoration: underline;
  color: #55c500;
}



